
.cartcontrol {
  font-size: 0;
}
.cartcontrol .cart-decrease {
  -webkit-transition: all .3s linear;
  transition: all .3s linear;
  position: absolute;
  left: -36px;
}
.cartcontrol .cart-decrease.show-cart-decrease-enter-active,
.cartcontrol .cart-decrease.show-cart-decrease-leave-active {
  opacity: 1;
  -webkit-transform: rotate(0);
          transform: rotate(0);
  left: -36px;
}
.cartcontrol .cart-decrease.show-cart-decrease-enter,
.cartcontrol .cart-decrease.show-cart-decrease-leave-active {
  opacity: 0;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  left: 6px;
}
.cartcontrol .cart-decrease,
.cartcontrol .cart-add {
  display: inline-block;
  padding: 6px;
  line-height: 24px;
  font-size: 24px;
  color: #00a0dc;
}
.cartcontrol .cart-count {
  display: inline-block;
  font-size: 24px;
  color: #00a0dc;
  vertical-align: top;
  width: 12px;
  padding-top: 6px;
  line-height: 24px;
  text-align: center;
  font-size: 10px;
  color: #93999f;
}



